.areas_section{
    background-color: white;
    padding: 3% 3% 7% 3%;
    justify-content: center;
    text-align: center;
    justify-items: center;
    margin-top: 1rem;
    /* margin-bottom: 2rem; */
    font-family: 'Poiret One', cursive;
    box-sizing: border-box;
}

.areas_section .title_img{
    margin-left: 48%;
}

.areas{
    display: grid;
    grid-template-columns: repeat(2, 400px);
    /* column-gap: 2rem; */
    box-sizing: border-box;
    justify-content: center;
}

.areas_title{
    margin-top: 2rem;
    font-size: 3rem;
    font-weight: bolder;
    text-transform: uppercase;
    color: rgb(13, 27, 44);
    text-align: center;
    margin-bottom: 3rem;
}

.areas_card{
    margin: 1.5rem 0.5rem;
    text-align: center;
    /* width: max-content; */
    height: auto;
    /* border: 2px solid rgb(58, 87, 126); */
    background-color: white;
    box-shadow: 5px 5px 9px 4px rgb(138, 138, 138);
    border-radius: 2rem;
    padding: 2rem 1.5rem;
}
