.areas_card-title {
    font-size: 2rem;
    font-weight: bolder;
    /* text-align: center; */
    color: rgb(58, 87, 126);
}
.areas_card-title:hover{
    transition: 0.2s ;
    color: rgb(13, 27, 44);
    border-right: 2px solid rgb(13, 27, 44);
    border-bottom: 2px solid rgb(13, 27, 44);
    border-bottom-right-radius: 1.2rem;
    padding: 0rem 0.6rem 0.4rem 0rem;
}

.areas_card p , .areas_card2 p{
    font-size: 1.2rem;
}



.areas_card-img{
    background-image: url(../Jobs/Family-Law-Icon.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100px;
    height: 100px;
    text-align: center;
    margin: 1rem auto;
    margin-bottom: 1.5rem;
}
