#areas_item{
    background-color: white;
    margin-top: 1rem;
    padding: 3% 8% 8% 8%;
    font-family: 'Poiret One', cursive;
    text-align: center;
}

#areas_item h2{
    margin: 2.5rem 0rem; 
    font-size: 2.5rem;
    font-weight: bolder;
    color: rgb(13, 27, 44);
    text-align: center;
}

#areas_item h3{
    font-weight: bolder;
    font-size: 1.5rem;
    color: rgb(58, 87, 126);
    letter-spacing: 1px;
    margin-top: 4.5rem;
}

#areas_item p{
    font-weight: bolder;
}


@media screen and (max-width:1175px){
    .areas{
        box-sizing: border-box;
        display: grid;
        grid-template-columns: repeat(2, 400px);
    }
    .areas_card{
        /* width: 480px; */
        box-sizing: border-box;
    }
}
@media screen and (max-width:1090px){
    .areas_section{
        justify-content: center;
        /* margin: auto; */
    }
    .areas_section .title_img{
        margin-left: 47%;
    }
    .areas{
        display: grid;
        box-sizing: content-box;
        /* grid-template-columns: 370px 370px; */
    }
    .areas_card{
        box-sizing: border-box;
        width: auto;
    }
}
@media screen and (max-width:985px){
    .areas_section{
        padding: 3% 13% 7% 13%;
        /* grid-template-columns: 200px 200px; */
    }
    .areas_card-title{
        font-size: 2.5rem;
    }
    .areas_card p{
        font-size: 1.7rem;
    }
    .areas{
        display: block;
    }
}
@media screen and (max-width:800px){
    .areas_section .title_img{
        margin-left: 45%;
    }
    .areas_card p{
        font-size: 1.4rem;
    }
}
@media screen and (max-width:629px){
    .areas_title{
        font-size: 2.5rem;
    }
}
@media screen and (max-width:548px){
    .areas_section{
        padding: 3% 7%;
    }
    .areas_title{
        font-size: 1.8rem;
    }
    .areas_card p{
        font-size: 1.2rem;
    }
    .areas_card-title{
        font-size: 2rem;
    }
    .areas_card{
        padding: 1rem ;
    }
}
